*{margin:0;padding: 0;}
.main_div{width:100%;height: 100vh;background-color: #6983aa;display: flex;flex-direction:row;justify-content: center;align-items: center;text-align:center;}
.center_div{width:25%;height: 80vh;background-color:#f4f4f4;box-shadow: 5px 5px 25px -5px rgba(0,0,0,0.5);border-radius: 15px;}
h1{color:#fff;background-color: transparent;background-color: #8566aa;padding: 6px 0 2px 0;margin-bottom:10px;box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3);}
input{text-align: center;height: 30px;top:10px;border:none;background-color: transparent;font-size: 20px;font-weight:500;width: 60%;border-bottom: 2px solid #8566aa;outline: none;}
button{min-height: 40px;width: 40px;border-radius: 50%;border-color: transparent;background-color: #8566aa;color:#fff;font-size: 40px;border:none;outline: none;
  margin-left:10px;box-shadow: 5px 5px 25px -5px rgba(0,0,0,0.5);}
button:hover{background-color: #20bf6b}
ol{margin-top: 30px;}
ol li{padding-left:0;text-align:left;font-size:20px;font-weight: 500;min-height: 40px;display: flex;align-items: center;color:#8566aa;text-transform: capitalize;}
.todo_style{display: flex;flex-direction: row;justify-content:left;align-items: center;}
.todo_style .fa-times{width: 20px;height:20px;display: flex;justify-content: center;align-items: center;background-color: #8566aa;border-radius: 50%;margin:0 15px 0 35px;color:#fff;box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3);}
.todo_style .fa-times:hover{background-color: crimson}
.todo_style .fa-times:hover .list_style{color:crimson;}

@media (max-width: 640px){
  .center_div{width: 70%;}
}
